/* Global Styles */
body {
  font-family: 'Roboto', sans-serif;
  background-color: #f5f5f5;
  color: #333333;
  margin: 0;
  padding: 0;
}

h1, h2 {
  font-weight: bold;
  color: #4A90E2;
  text-align: center;
}

input, textarea {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

button {
  background-color: #4A90E2;
  color: white;
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  width: 100%;
}

button:hover {
  background-color: #357ABD;
}

.container {
  max-width: 500px;
  margin: 0 auto;
  padding: 20px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.message-list {
  padding: 10px;
  background-color: #fff;
  margin-top: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-height: 400px;
  overflow-y: auto;
}

.message {
  padding: 10px;
  margin: 10px 0;
  background-color: #e1f5fe;
  border-radius: 10px;
}

.message p {
  margin: 0;
}

/* Responsive */
@media only screen and (max-width: 600px) {
  .container {
    max-width: 90%;
  }

  button {
    padding: 15px;
    font-size: 18px;
  }

  input, textarea {
    font-size: 18px;
    padding: 15px;
  }
}
